@import url(https://fonts.googleapis.com/css?family=Poppins:400,600,800|Lato:400,600,800|Raleway:400,600,800|Roboto:400,500,700,900|Space+Mono:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}


/* FONTS GOOGLE IMPORT */

.Title{
    font-family: 'Raleway', sans-serif;
    font-size: 6em;
    padding-top: 1em;
    padding-bottom: 1em;
}

.SubTitle{
    font-family: 'Lato', sans-serif;
    font-size: 2em;
}

.Page{
    width: 80%;
    height: 600px;
    margin-left: auto;
    margin-right: auto;
}

.PageDetail{
    width: 80%;
    height: 600px;
    margin-left: auto;
    margin-right: auto;
}

.TitleAboutMe{
    font-family: 'Raleway', sans-serif;
    font-size: 2em;
    font-weight: bold;
    text-align: left;
}

.TextAboutMe{
    font-family: 'Lato', sans-serif;
    font-size: 1em;
    text-align: left;
}




